<script>
// import feather from 'feather-icons';
import NotificationAlertas from '@/components/Notifications/NotificationAlerts.vue';
import PerfectScrollbar from 'perfect-scrollbar';
import 'perfect-scrollbar/css/perfect-scrollbar.css';

export default {
  name: 'dashboard',
  components: {
    NotificationAlertas,
  },
  data() {
    return {
      user: null,
      isOpen: null,
      sidebarOpen: false,
    }
  },
  computed: {
    isAbogado() {
      return this.user?.usu_rol === 'ABOGADO';
    },
    rol() {
      return this.$store.getters.get__user.usu_rol;
    },
    name() {
      return this.$store.getters.get__user.name;
    },
  },
  mounted() {
    const container = document.querySelector('.navbar-content');
    const ps = new PerfectScrollbar(container);

    // this.$once('hook:beforeDestroy', () => {
    //   ps.destroy();
    // });

    // this.$nextTick(() => {
    //   feather.replace();
    // });
  },

  created() {
    // const { name, usu_rol } = this.$store.getters.get__user;
    // this.user = name;
    // this.rol = usu_rol;

    // if (!this.user) {
    //   this.$router.push({ path: '/' });
    // }
  },
  methods: {
    async logout() {
      try {
        this.$showLoadingAlert('', 'Cerrando Sesión...');
        const formData = {
          id: null
        };
        const response = await this.$postData('demandante/logout', this, formData);

        if (response.state === 0) {
          this.$store.commit('mt_logout_auth');
          this.$router.replace({ path: '/' });
        } else {
          this.$errorAlert('', 'Error en la respuesta del servidor');
        }
      } catch (error) {
        this.$closeLoadingAlert();
        this.$handleApiError(error, 'Error en la solicitud de cerrar sesión');
        this.$errorAlert('', error.message);
      } finally {
        this.$closeLoadingAlert();
      }
    },
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
    },
    closeSidebar() {
      this.sidebarOpen = false;
    },

    showOption(...allowedRoles) {
      return allowedRoles.includes(this.rol);
    },

    toggleMenu(menu) {
      this.isOpen = this.isOpen === menu ? null : menu;
      this.saveSelected();
    },

    saveSelected() {
      localStorage.setItem('isOpen', this.isOpen);
    },

    loadSelect() {
      const isOpen = localStorage.getItem('isOpen');
      if (isOpen !== null) {
        this.isOpen = isOpen;
      }
    },

    clearLocalStorage() {
      localStorage.removeItem('isOpen');
    },

    mtdLogout() {
      this.$store.commit('mt_logout_auth');
      this.$router.replace({ path: '/' });
      this.clearLocalStorage();
    },

    getRouteHome() {
      return this.$route.path === '/home';
    },

    getRouteExpedienteRegistro() {
      const routesToCheck = [
        '/expediente/registro/civil-laboral',
        '/expediente/registro/penal',
        '/expediente/registro/arbitral',
        '/expediente/registro/indecopi'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpedienteActualizacion() {
      const routesToCheck = [
        '/expediente/actualizacion/civil-laboral/',
        '/expediente/actualizacion/penal/',
        '/expediente/actualizacion/arbitral/',
        '/expediente/actualizacion/indecopi/'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpedienteBuscar() {
      const routesToCheck = [
        '/expediente/busqueda',
        '/expediente/detalle/',
        '/expediente/lista',
        '/expediente/actualizacion/',
        '/eventos/',
        '/expediente/archivados'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpedienteArchivados() {
      const routesToCheck = [
        '/expediente/archivados',
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteExpediente() {
      if (this.getRouteExpedienteRegistro() || this.getRouteExpedienteBuscar() || this.getRouteExpedienteActualizacion()) {
        return true;
      }
    },

    isRouteActive(route) {
      const regex = new RegExp(`^${route}\\d*$`);
      return regex.test(this.$route.path);
    },

    getRouteDemandante() {
      const routesToCheck = [
        '/demandantes',
        '/historial/registro-demandante/',
        '/demandante/informacion/'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteDemandado() {
      const routesToCheck = [
        '/demandados',
        '/historial/registro-demandado/',
        '/demandado/informacion/'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRoutePersonas() {
      const routesToCheck = [
        '/personas'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteAbogado() {
      const routesToCheck = [
        '/abogados'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteEquipo() {
      const routesToCheck = [
        '/equipo'
      ]
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteCalendario() {
      return this.$route.path === '/calendario';
    },

    getRouteAudiencia() {
      return this.$route.path === '/audiencias';
    },

    getRouteAlerta() {
      return this.$route.path === '/alertas';
    },
    getRouteAgenda() {
      if (this.getRouteCalendario() || this.getRouteAudiencia() || this.getRouteAlerta()) {
        return true;
      }
    },

    getRouteReporte() {
      return this.$route.path === '/reportes';
    },

    getRouteMantenimiento() {
      const currentRoute = this.$route.path;
      return currentRoute.includes('/mantenimiento');
    },

    getRoutePanel() {
      return this.$route.path === '/panel';
    },

    getRouteEmail() {
      return this.$route.path === '/email/auth';
    },

    getRouteOficioBuscar() {
      const routesToCheck = [
        '/oficio/buscar',
        'oficio/lista',
        'oficio/detalle'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteOficioRegistro() {
      const routesToCheck = [
        '/oficio/registro'
      ];
      return routesToCheck.some(route => this.isRouteActive(route));
    },

    getRouteOficio() {
      if (this.getRouteOficioRegistro() || this.getRouteOficioBuscar()) {
        return true;
      }
    },
  }
}
</script>
